<template>
  <div class="swap-box">
    <HeadTop title="合成" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="img-box">
      <img v-if="!btn.loading || Number(kttAllowance) <=0" src="@/assets/images/merge.png" alt="">
       <img v-else-if="Number(kttAllowance) > 0 && btn.loading" src="@/assets/images/_4.gif" alt="">
    </div>
    <div class="btn-box">
      <van-button v-if="Number(kttAllowance) > 0" color="linear-gradient(270deg, #00DFB2 0%, #00C4DE 100%)" type="primary"  size="large" :loading="btn.loading" loading-text="正在合成..." block @click="mergeNft">合成</van-button>
      <van-button v-else color="linear-gradient(270deg, #00DFB2 0%, #00C4DE 100%)" type="primary" size="large" :loading="btn.loading" loading-text="正在授权..." block @click="approveKtt">授权</van-button>
    </div>
  </div>
  <van-dialog v-model:show="show" title="">
  <img class="show-img" src="@/assets/images/index_img.png" />
</van-dialog>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import swapAbi from "@/utils/swapAbi";
import tokenAbi from "@/utils/abi.json";
import { Toast } from 'vant';
import config from "@/utils/config";
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      voiceNftContract: null,
      show: false,
      kttAllowance: '',
       btn: {
        loading: false
      },
      initContract: () => {
        data.swapContract = new data.web3.eth.Contract(swapAbi, config.swapAddress)
        data.kttContract = new data.web3.eth.Contract(tokenAbi, config.kttAddress)
        data.initData()
      },
      initData: () => {
        data.getKttAllowance()
      },
       getKttAllowance: async () => {
        let allowance = await data.kttContract.methods.allowance(data.address, config.swapAddress).call()
        data.kttAllowance = allowance;
      },
      approveKtt: async () => {
        data.btn.swaploading = true
        try {
          await data.kttContract.methods.approve(config.swapAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getKttAllowance()
          data.btn.swaploading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.swaploading = false
        }
        
      },
      mergeNft: async () => {
        let balance = await data.kttContract.methods.balanceOf(data.address).call()
        let needAmount = await data.swapContract.methods.mergePrice().call()
        let kttDecimals = await data.kttContract.methods.decimals().call()
        let amount = new BigNumber(needAmount).dividedBy(10 ** kttDecimals).toFixed(2)
        if(Number(balance) < Number(needAmount)) {
          Toast(`KTT余额不足${amount}`)
          return
        }
        data.btn.loading = true
        try {
          await data.swapContract.methods.mergeNft().send({from: data.address})
          Toast('合成成功')
          data.show = true
          data.btn.loading = false
        } catch (error) {
          console.log(error)
          Toast('合成失败')
          data.btn.loading = false
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.swap-box{
  min-height: calc(100vh - 44px);
  background: url('~@/assets/images/merge_bg.png') no-repeat;
  background-size: cover;
}
.show-img{
  display: block;
  width: 100%;
}
.img-box{
  // padding-top: 30px;
  min-height: 480px;
  img{
    display: block;
    width: 100%;
  }
}
.btn-box{
  padding: 60px 50px 0;
}
</style>
